import React from 'react'
import {Nav, NavLink, NavIcon, Bars} from "./NavBarElements.js"

export default function Index({toggle}) {
    return (
        <>
            <Nav>
                <NavLink to="/">Pizzaria</NavLink>
                <NavIcon onClick={toggle}>
                    <Bars/>
                </NavIcon>
            </Nav>
        </>
    )
}
