import styled from "styled-components"


export const Container = styled.div`
    height: fit-content;
    text-align: center;
    background: #000;
    color: #fff;
    margin-top: -20px;
    padding-bottom: 90px;

    h1{
        font-size: clamp(2rem, 3vw, 4rem);
        margin: 20px auto;
        text-transform: uppercase;
        color: #fff;
        font-family: 'Questrial', sans-serif;
    }
    hr{
        width: 20%;
        margin: 0 auto;
        margin-bottom: 30px;
        color: #fff;
    }

    p{
        width: 80%;
        font-family: 'Questrial', sans-serif;
        margin: 0 auto;
        font-size: 1.5rem;
    }

    .owl-theme{
        text-align: left;
        height: fit-content;
        
        h2, p{
            width: 80%;
            margin: 0 auto;
            font-family: 'Questrial', sans-serif;
            margin-top: 20px;
        }
        P{
            font-size: 1rem;
        }
        button{
            cursor: pointer;
            font-family: 'Questrial', sans-serif;
            font-size: clamp(1.5rem, 2.5vw, 1.8rem);
            padding: 5px 20px;
            background: #e31837;
            color: #fff;
            border-radius: 5px;
            margin-left: 50px;
            margin-top: 20px;
            border: none;

            &:hover{
                background:#ffc500;
            }
        }
    }
`
