import React from 'react'
import Hero from "../../Components/Hero/Index"
import Product from "../../Components/Products/Index"
import Featured from "../../Components/Feature/Index"
import Footer from "../../Components/Footer/Index"
import {productData} from "../../Components/Products/data"
import {productDataTwo} from "../../Components/Products/data"
import {OwlSlider} from "../../Components/SliderOwl/Index"

export const Home = () => {
    return (
        <>
            <Hero />
            <Product heading='Escolha a sua favorita' data={productData} />
            <Featured />
            <Product heading='Temos também Sanduíches' data={productDataTwo} />
            <OwlSlider />
            <Footer />
        </>
    )
}
